          isActive: false,
          description: "Просто поисковик картинок через бесплатный api.",
        },
      ],
    };
  },

  methods: {
    getTag(event) {
      if (!event.target.closest("li")) return;
      this.activeTag = event.target.dataset.info;
      this.showProjects();
    },
    showProjects() {
      this.activeProjects = [];
      this.projects.forEach((project) => {
        if (project.tags.includes(this.activeTag)) {
          this.activeProjects.push(project);
        }
      });
    },
    showActiveProject() {
      if (!event.target.closest("a")) return;

      this.activeProjects[
        parseInt(event.target.closest("a").dataset.index)
      ].isActive = !this.activeProjects[
        parseInt(event.target.closest("a").dataset.index)
      ].isActive;
    },
  },
};
</script>

<style scoped>
.show-examples ul {
